.container {
    /* border: 1px solid red; */
    display: flex;
    flex-direction: row;
    width: 90vw;
    height: 90vh;    
}

.container .editor-container {
    /* border: 1px solid blue; */
    width: 50%;
    flex-grow: 1;
    margin: 0 5px;
}

.container .editor-container textarea {
    border-radius: 15px; 
    padding: 5px 10px;
    outline: none;       
    font-size: 15px;
    background: #36312c;
    color: #ebd1b7;
}


.container .preview-container {
    /* border: 1px solid green; */
    font-size: 15px;
    width: 50%;
    margin: 0 5px;
    display: flex;    
    flex-direction: column;
}

.container .preview-container .content {
    border-radius: 15px;     
    overflow-y: scroll;
    text-align: left;
    height: 88vh;
}

.container .preview-container .content pre code {
    border-radius: 5px;

}

.container .preview-container .actions {
    /* border: 1px solid blue; */
    display: flex;
    justify-content: center;
}

.container .preview-container .actions button {
    width: 50%;
    margin: 0 5px;
}